*/\n/* 7. Footer */\n/*------------------------------------------------------------------------------------------*/\n#landing-footer {\n    clear: both;\n}\n#landing-footer .row {\n    padding: 0;\n}\n#landing-footer p {\n    margin: 0;\n}\n#landing-footer ul {\n    list-style: none;\n    text-align: right;\n}\n#landing-footer li {\n    display: inline-block;\n}\n/* Scroll Up */\n#scrollUp {\n    bottom: 15px;\n    right: 30px;\n    width: 70px;\n    height: 80px;\n    margin-bottom: -10px;\n    padding: 0 5px 20px 5px;\n    text-align: center;\n    text-decoration: none;\n    color: #fff;\n    transition: margin-bottom 150ms linear;\n}\n#scrollUp:hover {\n    margin-bottom: 0;\n}\n#scrollUp:before {\n    background: #ccc;\n    font-family: \"fontawesome\";\n    font-size: 2.4em;\n    font-style: normal;\n    font-weight: normal;\n    text-align: center;\n    color: #fff;\n    width: 45px;\n    height: 45px;\n    display: inline-block;\n    line-height: 45px;\n    content: \"\\f106\";\n    opacity: 1;\n}\n/*Preloader*/\n#preloader {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: #fff; /* change if the mask should have another color then white */\n    z-index: 99999; /* makes sure it stays on top */\n}\n#status {\n    width: 110px;\n    height: 110px;\n    position: absolute;\n    left: 50%; /* centers the loading animation horizontally one the screen */\n    top: 50%; /* centers the loading animation vertically one the screen */\n    background-position: center;\n    margin: -55px 0 0 -55px; /* is width and height divided by two */\n    text-align: center;\n}\n/*!\n * Load Awesome v1.1.0 (http://github.danielcardoso.net/load-awesome/)\n * Copyright 2015 Daniel Cardoso <@DanielCardoso>\n * Licensed under MIT\n */\n.la-ball-triangle-path,\n.la-ball-triangle-path > div {\n    position: relative;\n    box-sizing: border-box;\n}\n.la-ball-triangle-path {\n    display: block;\n    font-size: 0;\n    color: #fff;\n}\n