n/* Mobile to Tablet Portrait\n===============================*/\n@media (min-width: 480px) and (max-width: 767px) {\n\n    #wrapper {\n        margin:0;\n        padding:0;\n    }\n\n    .page-border, #scrollUp, #scrollUp:before {\n        display:none;\n    }\n\n\n    .row {\n        padding: 15px 0 !important;\n    }\n\n    .col-1, .col-2, .col-5, .col-7, .col-9, .col-11, .col-2-3, .col-3-4, .col-61, .col-38, .row {\n        width: 100%;\n    }\n\n    .col-3, .col-4, .col-6, .col-8, .col-10, .col-12 {\n        width: 50%;\n    }\n\n    /*-----------------Header Elements-----------------*/\n    #header .row {\n        padding: 0 !important;\n    }\n\n    #header aside {\n        display: inline-block;\n        position: absolute;\n        top: 40px;\n        right: 60px;\n        padding-top:3px;\n        padding-right: 5px;\n    }\n\n    #header.nav-solid aside {\n        top: 20px;\n    }\n\n    #header aside ul {\n        margin-top: 0 !important;\n        padding-top: 6px;\n    }\n\n    #banner-content.row {\n        padding-top: 90px !important;\n    }\n\n    /*Navigation*/\n    nav#nav-mobile ul {\n        margin-left: -40px;\n        margin-right: -40px;\n        padding-left: 20px;\n        padding-right: 20px;\n    }\n\n    /*-----------------Sections-----------------*/\n\n    /* Video */\n    .slvj-lightbox iframe, .slvj-lightbox object, .slvj-lightbox embed {\n        height: 370px !important;\n    }\n\n    /* Footer */\n    #landing-footer #copyright {\n        float: left;\n        width: 50%;\n    }\n\n    #landing-footer .social-icons {\n        float: right;\n        width: 50%;\n    }\n\n}\n/* Landscape Tablet to Desktop\n===============================*/\n@media (min-width: 768px) and (max-width: 1024px) {\n\n    #wrapper {\n        margin:0;\n        padding:0;\n    }\n\n    .page-border {\n        display:none;\n    }\n\n    .big-padding-top {\n        padding-top: 45px !important;\n    }\n\n    /*-----------------Header Elements-----------------*/\n    #header aside {\n        display: inline-block;\n        position: absolute;\n        top: 40px;\n        right: 60px;\n        padding-top:3px;\n        padding-right: 5px;\n    }\n\n    #header.nav-solid aside {\n        top: 20px;\n    }\n\n    #header aside ul {\n        margin-top: 0 !important;\n        padding-top: 6px;\n    }\n\n    /*Navigation*/\n    nav#nav-mobile ul {\n        margin-left: -40px;\n        margin-right: -40px;\n        padding-left: 20px;\n        padding-right: 20px;\n    }\n\n    /*-----------------Sections-----------------*/\n\n    /* Video */\n    .slvj-lightbox iframe, .slvj-lightbox object, .slvj-lightbox embed {\n        height: 432px !important;\n    }\n\n}\n/* Namari Landing Page Dynamic Style Index\n\n1. Website Default Styling\n2. Navigation\n3. Primary and Secondary Colors\n4. Banner\n5. Typography\n6. Buttons\n7. Footer\n\n\n/*------------------------------------------------------------------------------------------*/\n/* 1. Website Default Styling */\n/*------------------------------------------------------------------------------------------*/\nbody {\n    background:rgb(255, 255, 255);\n}\n/* Default Link Color */\na, .la-ball-triangle-path {\n    color:#d2b356;\n}\na:hover, #header.nav-solid nav a:hover {\n    color:#d2b356;\n}\n/* Default Icon Color */\n.icon i {\n    color:#d2b356;\n}\n/* Border Color */\n#banner .section-heading:before, .testimonial.classic footer:before {\n    background: #d2b356;\n}\n.pricing-block-content:hover {\n    border-color:#d2b356;\n}\n/*------------------------------------------------------------------------------------------*/\n/* 2. Navigation */\n/*------------------------------------------------------------------------------------------*/\n/* Transparent Navigation Color on a Banner */\n#header nav a, #header i {\n    color:#111;\n}\n/* Navigation Colors when the Navigation is sticky and solid */\n#header.nav-solid, #header.nav-solid a, #header.nav-solid i, #nav-mobile ul li a {\n    color:#333;\n}\n/* Navigation Active State */\n#header.nav-solid .active {\n    color: #d2b356;\n    border-color: #d2b356;\n}\n/*------------------------------------------------------------------------------------------*/\n/* 3. Primary and Secondary Colors */\n/*------------------------------------------------------------------------------------------*/\n/* Primary Background and Text Colors */\n.primary-color, .featured .pricing {\n    background-color:#d2b356;\n}\n.primary-color, .primary-color .section-title, .primary-color .section-subtitle, .featured .pricing, .featured .pricing p {\n    color:rgb(248, 248, 248);    \n}\n.section-heading h2:after {\n    background:#d2b356;\n    content:\"\";\n    display:block;\n    width:30px;\n    height:5px;\n    margin-top:30px;\n}\n.text-center .section-heading h2:after {\n    margin:30px auto 25px auto;\n}\n/* Primary Icon Colors */\n.primary-color .icon i, .primary-color i {\n    color:#fff;\n}\n/* Secondary Background and Text Colors */\n.secondary-color {\n    background-color:#f5f5f5;\n}\n/*------------------------------------------------------------------------------------------*/\n/* 4. Banner */\n/*------------------------------------------------------------------------------------------*/\n/* Banner Background and Text Colors */\n#banner {\n    background: url('cg3.jpg') no-repeat center top;\n    background-size:cover;\n   \n    \n}\n/*------------------------------------------------------------------------------------------*/\n/* 5. Typography */\n/*------------------------------------------------------------------------------------------*/\nbody {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size:15px;\n    font-weight:normal;\n    color:#111;\n}\n/* Logo, if you are using Fonts as Logo and not image\n\n#logo h1 {\n    font-family:;\n    font-size:; \n    font-weight:;\n    color:;\n}\n\n#logo h2 {\n    font-family:;\n    font-size:; \n    font-weight:;\n    color:;\n}\n\n*/\n/* Banner Typography */\n#banner h1 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size:62px;\n    line-height:60px;\n    font-weight:800;\n    color:#111;\n}\n#banner h2 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size:18px;\n    font-weight:300;\n    color:#111;\n}\n/* Section Title and Subtitle */\n.section-title {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 34px; \n    font-weight:700;\n    color:#111;\n}\n.section-subtitle {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 16px;\n    font-weight:300;\n    color:#9c9c9c;\n}\n/* Testimonial */\n.testimonial q {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 17px; \n    font-weight:300;\n}\n.testimonial.classic q, .testimonial.classic footer {\n    color:#111;\n}\n/* Standard Headings h1-h6 */\nh1 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 40px; \n    font-weight:300;\n    color:#111;\n}\nh2 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 34px; \n    font-weight:300;\n    color:#111;\n}\nh3 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 30px; \n    font-weight:700;\n    color:#111;\n}\nh4 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 18px; \n    font-weight:400;\n    color:#111;\n}\nh5 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 16px; \n    font-weight:400;\n    color:#111;\n}\nh6 {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size: 14px; \n    font-weight:400;\n    color:#111;\n}\n/*------------------------------------------------------------------------------------------*/\n/* 6. Buttons */\n/*------------------------------------------------------------------------------------------*/\n/* ----------Default Buttons---------- */\n/* Button Text */\n.button, input[type=\"submit\"]  {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size:14px;\n    font-weight:bold;\n    color:#111;\n}\n/* Button Color */\n.button, input[type=\"submit\"] {\n    border-color:#111;\n}\n/* Button Hover Color */\n.button:hover,  input[type=\"submit\"]:hover {\n    border-color:#d2b356;\n    color:#d2b356;\n}\n/* ----------Banner Buttons---------- */\n/* Button Text */\n#banner .button {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size:16px;\n    color:#111;\n}\n/* Button Color */\n#banner .button {\n    border-color:#111;\n}\n/* Button Hover Color */\n#banner .button:hover {\n    color:#d2b356;\n    border-color:#d2b356;\n}\n/*------------------------------------------------------------------------------------------*/\n/* 7. Footer */\n/*------------------------------------------------------------------------------------------*/\n#landing-footer, #landing-footer p, #landing-footer a {\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\n    font-size:12px;\n    font-weight:normal;\n    color:#999;\n}\n/* Footer Icon Color */\n#landing-footer i {\n    color:#999;\n}"